.dg_icon-button {
  border: 1px solid #333;
  background-color: #fff;
  color: #333;
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s ease 0s; }
  .dg_icon-button i,
  .dg_icon-button svg {
    font-size: 36px;
    color: #feb30c;
    display: block;
    margin: auto;
    padding: 0 10px 20px; }
  .dg_icon-button:hover,
  .dg_icon-button:hover i,
  .dg_icon-button:hover svg {
    color: #fff; }
  .dg_icon-button:hover {
    background-color: #002280; }

